/* ******************************* */
/* Navbar */
/* ******************************* */

.navbar_main_container {
  position: fixed;
  top: 0;
  width: 100vw;
  z-index: 10000000;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, .15);


}

.short_nav_container {
  height: 75px;
  background-color: #f5f5f5;
  align-items: center;
  display: flex;
  padding: 0px 30px;
  display: flex;
  justify-content: space-between;

}

.main_nav_logo {
  height: 60px;
  margin-left: 20px;
}

.main_nav_logo img {
  /* width: 100%; */
  /* width: 100%; */
  height: 100%;
}




.logo_text {
  align-items: center;
  display: flex;
}

.logo_text h1 {
  font-size: 36px;
  text-align: center;
  font-weight: 600;
  /* font-family: 'Nunito', sans-serif; */
  font-family: 'Raleway', sans-serif;
  margin: 0px;
  color: rgb(32, 32, 32);
  text-shadow: 3px 3px 3px rgba(99, 99, 99, 0.2);
}

.navbar_container {
  height: 45px;
  background-color: var(--theme-color);
  /* box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.2); */
  display: flex;
  /* justify-content: center; */
  align-items: center;
  font-weight: 600;
  font-size: 20px;
  transition: all 0.5s;


}


.main_nav {
  width: 100%;
  height: 100%;
  display: flex;
  padding: 0px 30px;
  align-items: center;
}


.navbar_toggle {
  font-size: 28px;
  cursor: pointer;
  outline: none;
  border: none;
  padding: 5px;
  color: rgb(0, 0, 0);
  z-index: 10000;
  /* margin-right: 20px; */
  display: none;
}



.nav_content {
  width: 100%;
  /* height: auto; */
  display: flex;
  align-items: center;


  justify-content: space-between;
}




.nav_list {
  display: flex;
  align-items: center;
}

.nav_list a {
  color: rgb(254, 254, 254);
  display: flex;
  align-items: center;
}


.nav_item {
  align-items: center;
  margin-right: 5px;
  padding: 0px 5px;
  line-height: 45px;
  font-size: 16px;
}




.nav_item:hover {
  transition: ease-in 0.5s;
  /* background-color: whitesmoke; */

  background-color: var(--theme-hover-clor);
}

.active_nav:hover {
  transition: ease-in 0.5s;
  /* background-color: whitesmoke; */

  background-color: var(--theme-hover-clor);

}

.active_nav {
  align-items: center;
  margin-right: 5px;
  padding: 0px 5px;
  line-height: 45px;
  font-size: 16px;
  background-color: var(--theme-hover-clor);
}

.dropdown_nav {
  display: flex;
  align-items: center;
}

.dropdown_nav .fa {
  font-size: 10px;
  padding: 5px;
  font-weight: lighter;

}

.dropdown_content {
  display: none;
  position: absolute;
  top: 120px;
  z-index: 10;
  background-color: white;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
  border-radius: 3px 3px 0px 0px;
  border-top: 2px solid #bbbbbb;
  width: 220px;
  padding: 10px 0px;
}

.dropdown_link {
  padding: 10px 10px;
  font-size: 14px;
  width: 100%;
  display: inline-block;
}

.dropdown_link:hover {
  background-color: rgba(178, 175, 175, 0.2);
}

.dropdown_link {
  color: black;
}

.dropdown_link:hover {
  color: var(--theme-color);
}

.dropdown_nav:hover+.dropdown_content {
  display: block;
}

.dropdown_content:hover {
  display: block;
}

.dropdown_content a {
  color: black !important;
}












