.VisitButton {
  background: linear-gradient(to left, #86be40, #00c6ff);
  border: none;
  border-radius: 30px;
  text-align: center;
  font-size: 16px;
  padding: 12px 15px;
  width: 150px;
  margin-top: 30px;
  transition: all 0.5s;
  cursor: pointer;
  align-items: center;
  font-family: 'Ubuntu', sans-serif;
}

.VisitButton a span {
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.5s;
  color: #FFFFFF;
}

.VisitButton span:after {
  content: '\00bb';
  position: absolute;
  opacity: 0;
  top: 0;
  right: -20px;
  transition: 0.5s;
}

.VisitButton :hover span {
  padding-right: 25px;
  color: white;
}

.VisitButton :hover span:after {
  opacity: 1;
  right: 0;
  color: white;

}

